import React from 'react';

const Dry = () => (
    <div>
        <h3>Принцип программирования DRY — don’t repeat yourself / не повторяйте себя</h3>
        <p>
            <a href="https://web-creator.ru/articles/dry">Статья</a>
        </p>
        <p>
            Следование принципу программирования «DRY» позволяет добиться высокой сопровождаемости проекта, простоты
            внесения изменений и качественного тестирования.
        </p>
        <p>
            Если код не дублируется, то для изменения логики достаточно внесения исправлений всего в одном месте и проще
            тестировать одну (пусть и более сложную) функцию, а не набор из десятков однотипных. Следование принципу DRY
            всегда приводит к декомпозиции сложных алгоритмов на простые функции. А декомпозиция сложных операций на
            более простые (и повторно используемые) значительно упрощает понимание программного кода. Повторное
            использование функций, вынесенных из сложных алгоритмов, позволяет сократить время разработки и тестирования
            новой функциональности.
        </p>
        <p>
            Следование принципу DRY приводит к модульной архитектуре приложения и к чёткому разделению ответственности
            за бизнес-логику между программными классами. А это — залог сопровождаемой архитектуры. Хотя чаще не DRY
            приводит к модульности, а уже модульность, в свою очередь, обеспечивает принципиальную возможность
            соблюдения этого принципа в больших проектах.
        </p>

        <p>
            В рамках одного программного класса (или модуля) следовать DRY и не повторяться обычно достаточно просто.
            Также не требует титанических усилий делать это в рамках небольших проектов, где все разработчики «владеют»
            всем кодом системы. А вот в больших проектах ситуация с DRY несколько сложнее — повторы чаще всего
            появляются из-за отсутствия у разработчиков целостной картины или несогласованности действий в рамках
            команды. Следовать принципу «don’t repeat yourself» в рамках больших проектов не так просто, как это может
            показаться на первый взгляд. От разработчиков требуется тщательное планирование архитектуры, а от
            архитектора или тимлида требуется наличие видения системы в целом и чёткая постановка задач разработчикам.
        </p>

        <p>
            В пректировании DRY тоже имеет место — доступ к конкретному функционалу должен быть доступен в одном месте,
            унифицирован и сгруппирован по какому-либо принципу, а не «разбросан» по системе в произвольных вариациях.
            Этот подход пересекается с принципом единственной ответственности из пяти принципов SOLID, сформулированных
            Робертом Мартином.
        </p>
    </div>
);

export default Dry;
