import React from 'react';
import ScrollTo from '../../../helperComponent/scrollTo';

const Functional = () => (
    <div>
        <h3>Итак, ФП!</h3>
        <h3>Полезные видео по ФП в джс, сравнение имеративного и фс. Функторы, монады</h3>
        <div>
            <iframe
                width="560"
                height="315"
                src="https://www.youtube.com/embed/0JxSs_GcvbQ"
                frameborder="0"
                allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
                allowfullscreen></iframe>
        </div>
        <b></b>
        <div>
            <iframe
                width="560"
                height="315"
                src="https://www.youtube.com/embed/3Z7f0Gi8pxw"
                frameborder="0"
                allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
                allowfullscreen></iframe>
        </div>
        <h3>Вики говорит нам:</h3>
        <div>
            <div>
                <strong>
                    Функциона́льное программи́рование — раздел дискретной математики и парадигма программирования, в
                    которой процесс вычисления трактуется как вычисление значений функций в математическом понимании
                    последних (в отличие от функций как подпрограмм в процедурном программировании).
                </strong>
            </div>
            <div>
                <strong>
                    Противопоставляется парадигме императивного программирования, которая описывает процесс вычислений
                    как последовательное изменение состояний{' '}
                </strong>
                (в значении, подобном таковому в теории автоматов). При необходимости, в функциональном программировании
                вся совокупность последовательных состояний вычислительного процесса представляется явным образом,
                например, как список.
            </div>
            <div>
                <strong>
                    Функциональное программирование предполагает обходиться вычислением результатов функций от исходных
                    данных и результатов других функций, и не предполагает явного хранения состояния программы.
                    Соответственно, не предполагает оно и изменяемость этого состояния (в отличие от императивного, где
                    одной из базовых концепций является переменная, хранящая своё значение и позволяющая менять его по
                    мере выполнения алгоритма).
                </strong>
            </div>
            <div>
                На практике отличие математической функции от понятия «функции» в императивном программировании
                заключается в том, что императивные функции могут опираться не только на аргументы, но и на состояние
                внешних по отношению к функции переменных, а также иметь побочные эффекты и менять состояние внешних
                переменных. Таким образом,
                <strong>
                    {' '}
                    в императивном программировании при вызове одной и той же функции с одинаковыми параметрами, но на
                    разных этапах выполнения алгоритма, можно получить разные данные на выходе из-за влияния на функцию
                    состояния переменных. А в функциональном языке при вызове функции с одними и теми же аргументами мы
                    всегда получим одинаковый результат: выходные данные зависят только от входных.
                </strong>{' '}
                Это позволяет средам выполнения программ на функциональных языках кешировать результаты функций и
                вызывать их в порядке, не определяемом алгоритмом и распараллеливать их без каких-либо дополнительных
                действий со стороны программиста (что обеспечивают функции без побочных эффектов — чистые функции).
            </div>
            <h3>Терминология</h3>
            <div>
                <strong>Функции высших порядков</strong>
            </div>
            <div>
                <strong>
                    Функции высших порядков — это такие функции, которые могут принимать в качестве аргументов и
                    возвращать другие функции
                </strong>
                . Математики такую функцию чаще называют оператором, например, оператор взятия производной или оператор
                интегрирования. Функции высших порядков позволяют использовать{' '}
                <strong>
                    карринг — преобразование функции от пары аргументов в функцию, берущую свои аргументы по одному.
                </strong>{' '}
                Это преобразование получило своё название в честь Х. Карри.
            </div>
            <p></p>
            <div>
                <strong>Чистые функции</strong>
            </div>
            <div>
                <strong>
                    {' '}
                    Чистыми называют функции, которые не имеют побочных эффектов ввода-вывода и памяти (они зависят
                    только от своих параметров и возвращают только свой результат).
                </strong>{' '}
                Чистые функции обладают несколькими полезными свойствами, многие из которых можно использовать для
                оптимизации кода:
            </div>
            <ul className="row list_style">
                <li>
                    Если результат чистой функции не используется, её вызов может быть удалён без вреда для других
                    выражений.
                </li>
                <li>
                    Результат вызова чистой функции может быть мемоизирован, то есть сохранён в таблице значений вместе
                    с аргументами вызова. Если в дальнейшем функция вызывается с этими же аргументами, её результат
                    может быть взят прямо из таблицы, не вычисляясь (иногда это называется принципом прозрачности
                    ссылок). Мемоизация, ценой небольшого расхода памяти, позволяет существенно увеличить
                    производительность и уменьшить порядок роста некоторых рекурсивных алгоритмов.
                </li>
                <li>
                    Если нет никакой зависимости по данным между двумя чистыми функциями, то порядок их вычисления можно
                    поменять или распараллелить (говоря иначе вычисление чистых функций удовлетворяет принципам
                    thread-safe)
                </li>
                <li>
                    Если весь язык не допускает побочных эффектов, то можно использовать любую политику вычисления. Это
                    предоставляет свободу компилятору комбинировать и реорганизовывать вычисление выражений в программе
                    (например, исключить древовидные структуры).
                </li>

                <div>
                    <strong>Рекурсия</strong>
                </div>
                <div>
                    В функциональных языках цикл обычно реализуется в виде рекурсии. Строго говоря, в функциональной
                    парадигме программирования нет такого понятия, как цикл. Рекурсивные функции вызывают сами себя,
                    позволяя операции выполняться снова и снова.
                </div>
                <p></p>
            </ul>
            <p></p>
            <h3>Особенности</h3>
            <p>
                <strong>
                    Императивные программы имеют склонность акцентировать последовательности шагов для выполнения
                    какого-то действия, а функциональные программы к расположению и композиции функций, часто не
                    обозначая точной последовательности шагов
                </strong>
            </p>
            <p>
                <strong>Основной особенностью</strong> функционального программирования, определяющей как преимущества,
                так и недостатки данной парадигмы, является то, что в ней{' '}
                <strong>реализуется модель вычислений без состояний.</strong> Если императивная программа на любом этапе
                исполнения имеет состояние, то есть совокупность значений всех переменных, и производит побочные
                эффекты, то{' '}
                <strong>
                    чисто функциональная программа ни целиком, ни частями состояния не имеет и побочных эффектов не
                    производит.
                </strong>{' '}
                То, что в императивных языках делается путём присваивания значений переменным, в функциональных
                достигается путём передачи выражений в параметры функций. Непосредственным следствием становится то, что
                чисто функциональная программа не может изменять уже имеющиеся у неё данные, а может лишь порождать
                новые путём копирования и/или расширения старых. Следствием того же является отказ от циклов в пользу
                рекурсии.
            </p>
            <h3>Сильные стороны</h3>
            <ul className="row list_style">
                <li>
                    <p>
                        <strong>Повышение надёжности кода</strong>
                    </p>
                    Привлекательная сторона вычислений без состояний —{' '}
                    <strong>
                        повышение надёжности кода за счёт чёткой структуризации и отсутствия необходимости отслеживания
                        побочных эффектов. Любая функция работает только с локальными данными и работает с ними всегда
                        одинаково, независимо от того, где, как и при каких обстоятельствах она вызывается.
                        Невозможность мутации данных при пользовании ими в разных местах программы исключает появление
                        труднообнаруживаемых ошибок{' '}
                    </strong>
                    (таких, например, как случайное присваивание неверного значения глобальной переменной в императивной
                    программе).
                    <p></p>
                </li>

                <li>
                    <p>
                        <strong>Удобство организации модульного тестирования</strong>
                    </p>
                    Поскольку функция в функциональном программировании не может порождать побочные эффекты, менять
                    объекты нельзя как внутри области видимости, так и снаружи (в отличие от императивных программ, где
                    одна функция может установить какую-нибудь внешнюю переменную, считываемую второй функцией).
                    Единственным эффектом от вычисления функции является возвращаемый ей результат, и единственный
                    фактор, оказывающий влияние на результат — это значения аргументов.
                    <div></div>
                    <strong>
                        Таким образом, имеется возможность протестировать каждую функцию в программе, просто вычислив её
                        от различных наборов значений аргументов. При этом можно не беспокоиться ни о вызове функций в
                        правильном порядке, ни о правильном формировании внешнего состояния. Если любая функция в
                        программе проходит модульные тесты, то можно быть уверенным в качестве всей программы.
                    </strong>
                    В императивных программах проверка возвращаемого значения функции недостаточна: функция может
                    модифицировать внешнее состояние, которое тоже нужно проверять, чего не нужно делать в
                    функциональных программах.
                    <p></p>
                </li>

                <li>
                    <p>
                        <strong>Возможности оптимизации при компиляции</strong>
                    </p>
                    Традиционно упоминаемой положительной особенностью функционального программирования является то, что
                    оно позволяет описывать программу в так называемом «декларативном» виде, когда жесткая
                    последовательность выполнения многих операций, необходимых для вычисления результата, в явном виде
                    не задаётся, а формируется автоматически в процессе вычисления функций. Это обстоятельство, а также
                    отсутствие состояний даёт возможность применять к функциональным программам достаточно сложные
                    методы автоматической оптимизации.
                    <p></p>
                </li>

                <li>
                    <p>
                        <strong>Возможности параллелизма</strong>
                    </p>
                    Ещё одним преимуществом функциональных программ является то, что они предоставляют широчайшие
                    возможности для автоматического распараллеливания вычислений.{' '}
                    <strong>
                        Поскольку отсутствие побочных эффектов гарантировано, в любом вызове функции всегда допустимо
                        параллельное вычисление двух различных параметров — порядок их вычисления не может оказать
                        влияния на результат вызова.
                    </strong>
                    <p></p>
                </li>
            </ul>
            <h3>Недостатки</h3>
            <p></p>
            Недостатки функционального программирования вытекают из тех же самых его особенностей. Отсутствие
            присваиваний и замена их на порождение новых данных приводят к необходимости постоянного выделения и
            автоматического освобождения памяти, поэтому в системе исполнения функциональной программы обязательным
            компонентом становится высокоэффективный сборщик мусора. Нестрогая модель вычислений приводит к
            непредсказуемому порядку вызова функций, что создаёт проблемы при вводе-выводе, где порядок выполнения
            операций важен. Кроме того, очевидно, функции ввода в своём естественном виде (например, getchar из
            стандартной библиотеки языка C) не являются чистыми, поскольку способны возвращать различные значения для
            одних и тех же аргументов, и для устранения этого требуются определённые ухищрения. Для преодоления
            недостатков функциональных программ уже первые языки функционального программирования включали не только
            чисто функциональные средства, но и механизмы императивного программирования (присваивание, цикл, «неявный
            PROGN» были уже в Лиспе). Использование таких средств позволяет решить некоторые практические проблемы, но
            означает отход от идей (и преимуществ) функционального программирования и написание императивных программ на
            функциональных языках. В чистых функциональных языках эти проблемы решаются другими средствами, например, в
            языке Haskell ввод-вывод реализован при помощи монад — нетривиальной концепции, позаимствованной из теории
            категорий.
        </div>
        <p></p>
        <div>
            <a href="https://habr.com/ru/post/142351/"> Более развернуто о ФП можно почитать тут</a>
        </div>
        <h3>Мякотка</h3>
        <p>
            Ок,это все были общие положения относитльно ФП. Но что может показаться интерсным нам, как js-разработчикам?
        </p>
        <div>
            Смотрим <ScrollTo text="видео" top={150} left={0} /> тов. Шемсединова и читаем{' '}
            <a href="https://tproger.ru/translations/functional-js-1/">статью эту </a>и{' '}
            <a href="https://tproger.ru/translations/functional-js-2/">и эту </a>
            чтобы разобраться что такое функторы и монады. Какие они бывают и какие задачи решают.{' '}
        </div>
        <h3>Функторы</h3>
        <p>
            Если просто, функторы- это некая сущность(в js это можно реализовать через классы\протитипы\ и замыкания)
            которая реализует метод map.
            <div>
                Вспоминаем Array.map и пониамем о чем идет речь - то, что возвращает НОВЫЙ МОДИФИЦИРОВАННЫЙ объект
            </div>
            <div>
                <img src={require('./assets/functor.png')} />
            </div>
            <div>
                Обратим внимание на слово НОВЫЙ. Мы не мутируем, не изменяем текущий объект. Это одна из основ ФП, как
                ясно из теории.
            </div>
            <div>
                Наш map действует точно также, как и привычный в Array.prototype - он принимает некотрую функцию и
                согласно ее алгоритму возвращает объект. Так что многие следовают функциональному стилю, даже не
                задумываясь об этом. Также тут есть страховка, в виде проверки. Если чего-то не хватает - вернем Null.
            </div>
            <h3>Монады</h3>
            <p>
                Монады — это подтип функторов, так как у них есть метод map, но они также реализуют другие методы,
                например, ap, of, chain.
                <div>
                    <img src={require('./assets/monad.png')} />
                </div>
            </p>
        </p>
        <h3>Выводы</h3>
        <div>
            JS- мультипарадигменный язык, никто не мешает нам использовать лучшие практики с раличных парадигм и
            совмещать их. Никто не заставляет писать чисто в одной парадигме.(да и писать чисто на ФП === невозможно,
            так как много токостей, допустим отрицание переменных)
        </div>
        <div>
            Используя элмененты ФП мы можем избегать ошибок, свзанных с мутацией. Мы лучше контрлируем происходящее. Мы
            можем упрощать юнит-тестирование. Создавая переиспользуемые функции, мы делаем код лаконичней и красивее.
            Следуем принципу dry. Каррирование позволяет нам удобнее выстраивать систему взаимодействия(основа паттерна
            декоратор в js).Ну а рекурсия обычно короче и красивей.
        </div>
    </div>
);
export default Functional;
