import React from 'react'

const Kiss = () => <div>
    <h3>KISS</h3>

    <div style={{marginTop:'70px'}}>
    KISS (акроним для «Keep it simple, stupid») — принцип проектирования, принятый в ВМС США в 1960. Принцип KISS утверждает, что большинство систем работают лучше всего, если они остаются простыми, а не усложняются. Поэтому в области проектирования простота должна быть одной из ключевых целей, и следует избегать ненужной сложности. Фраза ассоциировалась с авиаконструктором Кларенсом Джонсоном (1910—1990)[3]. В 1970-х гг. широко использовался термин «KISS-принцип» (англ. KISS principle)[4]. Вариации на фразу включают «англ. Keep it Simple, Silly», «keep it short and simple», «keep it simple and straightforward» и «keep it small and simple».
    </div>
    <div><img src={require('./assets/kiss.jpg')} style={{width:'130px',position:'absolute',top:0,right:0}}/> </div>
    <p>Итак. Что же такое KISS?</p>
    <p>Это не только замечательный вокально-инструментальный ансамбль,но и важный для разработчика принцип</p>
    Расписано <a href='https://web-creator.ru/articles/kiss'>тут</a> Но для тех кому лень переходить по ссылку скопипастю. Ибо смысла 
    пояснять что-либо нет.
    Все предельно ясно.
    <h3>Принцип программирования KISS — делайте вещи проще</h3>
    <p>Большая часть программных систем необосновано перегружена практически ненужными функциями, что ухудшает удобство их использование конечными пользователями, а также усложняет их поддержку и развитие разработчиками.
     Следование принципу «KISS» позволяет разрабатывать решения, которые просты в использовании и в сопровождении.</p>
     <p>
     KISS — это принцип проектирования и программирования, при котором простота системы декларируется в качестве основной цели или ценности. 
     Есть два варианта расшифровки аббревиатуры: «keep it simple, stupid» и более корректный «keep it short and simple».
     </p>
     <h3>В проектировании следование принципу KISS выражается в том, что:</h3>
     <ul className='list_style row'>
         <li>не имеет смысла реализовывать дополнительные функции, которые не будут использоваться вовсе или их использование крайне маловероятно,
              как правило, большинству пользователей достаточно базового функционала, а усложнение только вредит удобству приложения;</li>
         <li>не стоит перегружать интерфейс теми опциями, которые не будут нужны большинству пользователей, гораздо проще предусмотреть для них отдельный «расширенный» интерфейс (или вовсе отказаться от данного функционала);</li>
         <li>
         бессмысленно делать реализацию сложной бизнес-логики, которая учитывает абсолютно все возможные варианты поведения системы, пользователя и окружающей среды, — во-первых, это просто невозможно, а во-вторых, такая фанатичность заставляет собирать «звездолёт», что чаще всего иррационально с коммерческой точки зрения.
         </li>
     </ul>
     <h3>В программировании следование принципу KISS можно описать так(наматываем на ус!):</h3>
     <ul className='list_style row'>
         <li>не имеет смысла беспредельно увеличивать уровень абстракции, надо уметь вовремя остановиться;</li>
         <li>бессмысленно закладывать в проект избыточные функции «про запас», которые может быть когда-нибудь кому-либо понадобятся</li>
         <li>не стоит подключать огромную библиотеку, если вам от неё нужна лишь пара функций;</li>
         <li>декомпозиция чего-то сложного на простые составляющие — это архитектурно верный подход</li>
         <li>абсолютная математическая точность или предельная детализация нужны не всегда — большинство систем создаются не для запуска космических шаттлов, данные можно и нужно обрабатывать с той точностью, которая достаточна для качественного решения задачи, а детализацию выдавать в нужном пользователю объёме, а не в максимально возможном объёме.</li>
      </ul>   
      Размышления по теме <a href='https://habr.com/ru/post/249639/'>Здесь</a>
      <p>Мякотка с этой статьи:</p>
      <p>
      <strong>Итак, принцип проектирования KISS (keep it simple and straightforward) провозглашает, что простота кода – превыше всего, 
      потому что простой код – наиболее понятный.</strong>
      Практически все принципы проектирования направлены на достижение понятности кода. Нарушая какой-либо принцип проектирования, 
        вы уменьшаете понятность кода. Непонятный код автоматически вызывает у человека ощущение того, что код сложный, так как его сложно понимать
        и модифицировать. При нарушении любого из этих принципов также нарушается и принцип KISS. Поэтому можно говорить, что KISS включает почти
     все остальные принципы проектирования.
    Патерны проектирования описывают наиболее удачные, простые и понятные решения некоторых проблем. 
    Если вы используете паттерн проектирования там, где нет проблемы, которую решает данный паттерн – 
    то вы нарушаете KISS, внося ненужные усложнения в код. Если вы НЕ используете паттерн проектирования там, где есть проблема, 
    соответствующая паттерну – то вы опять-таки нарушаете KISS, делая код сложнее, чем он мог бы быть.
      </p>
      <p>
      <strong>На мой взгляд, принцип KISS может быть полезен лишь для начинающих проектировщиков</strong>, которые не знают или не понимают основных принципов 
      проектирования. KISS защищает от неверного использования принципов проектирования и паттернов. Поскольку принципы и паттерны предназначены 
      для увеличения понятности кода, то их правильное использование не может привести к уменьшению понятности кода. Однако если вы неверно
       понимаете принцип проектирования (например, истолковываете «не плодите лишних сущностей» как «плодите как можно меньше сущностей»), 
       или соблюдая один принцип нарушаете десяток других, то KISS может стать для вас надёжной подушкой безопасности. В остальных случаях 
       от KISS-а мало толку, т.к. он слишком общий и абстрактный. Остальные же принципы более конкретны и содержат более явные пути к 
       достижению понятности и простоты кода.
      </p>

      <p>
        Всвязи с тем, что представления разных людей о таком понятии как «простота» могут различаться, приобрели широкое распространение
         <strong> следующая заблуждения относительно KISS-a:</strong>
         <div> Заблуждение 1. Если считать, что простой код – это такой код, который проще всего написать, то можно истолковать, что принцип 
        KISS призывает писать первое что взбредёт в голову, вообще не задумываясь о проектировании.</div>
        <div>Заблуждение 2. Если считать, что простой код – это такой код, для написания которого требуется как можно меньше знаний, 
        то можно истолковать, что принцип KISS призывает не использовать паттерны проектирования. </div>
      </p>
</div>

export default Kiss
