import React from 'react';
import { Link } from 'react-router-dom';

const OOP = () => (
    <div>
        <h2>Итак, ооп!</h2>
        <p>
            Начнем с сухих определений. Далеко лазить не будем. Откроем википедию и посмотрим что она скажет нам об ООП
        </p>
        <p>
            <strong>
                Объе́ктно-ориенти́рованное программи́рование (ООП) — методология программирования, основанная на
                представлении программы в виде совокупности объектов, каждый из которых является экземпляром
                определённого класса, а классы образуют иерархию наследования.
            </strong>
        </p>
        <p>
            Спасибо, википедия, из этого мы можем сделать вывод что ООП оперирует обьектами, каждый объект несет ряд
            свойств и характеристик, по которым их можно объеденить. Тоесть у кажого объекта есть свой класс. Мы можем
            создавать новые объекты, наделяя их определенным классом. Это значит что новый объект получит(унаследует)
            всю мощь объекта - прородителя
        </p>
        <p>Хорошо. Но мы, как умные ребята и правильные посоны изучим и другие источники</p>
        <p>
            <strong>
                Центральными в ООП являются понятия класса и объекта. Образно говоря, ООП заключается не столько в
                использовании классов и объектов в программе, сколько в замене принципа программирования "от функции к
                функции" принципом программирования "от класса к классу". Технология ООП прежде всего накладывает
                ограничения на способы представления данных в программе. Любая программа отражает в них состояние
                физических предметов либо абстрактных понятий (назовем их объектами программирования), для работы с
                которыми она предназначена. В традиционной технологии варианты представления данных могут быть разными.
                В худшем случае программист может "равномерно размазать" данные о некотором объекте программирования по
                всей программе. В противоположность этому все данные об объекте программирования и его связях с другими
                объектами можно объединить в одну структурированную переменную. В первом приближении ее можно назвать
                объектом. Кроме того, с объектом связывается набор действий, иначе называемых методами. С точки зрения
                языка программирования это функции, получающие в качестве обязательного параметра указатель на объект.
                Технология ООП запрещает работать с объектом иначе, чем через методы, то есть внутренняя структура
                объекта скрыта от внешнего пользователя. Описание множества однотипных объектов называется классом.
                Объект - структурированная переменная, содержащая всю информацию о некотором физическом предмете или
                реализуемом в программе понятии. Класс - описание множества таких объектов и выполняемых над ними
                действий.
            </strong>
        </p>

        <p>
            Очень хорошо, так мы приходим к пониманию, что ООП это не столько про классы, сколько про СПОСОБ ОРГАНИЗАЦИИ
            КОДА
        </p>
        <p>
            Чтобы разобраться какие преймущества дает этот подход, рассмотрим основные ПРИНЦИПЫ ООП. Ты наверняка о них
            слышал{' '}
        </p>
        <p>
            По классике выделяют 3 принципа. Но в некоторых источниках число доходит до 8. Обозначим четыре. Добавим
            абстрагирование
        </p>
        <p></p>
        <p>
            <p>
                <div>
                    Начну с абстрагирования. Это очень важное в ООП понятие. Так как именно оно лежит в основе
                    возможности расширения приложений. В основе классов тоже лежит некая астракция(интерфейс), которая
                    реализуется в наследовании
                </div>
                <strong>
                    Абстрагирование — это способ выделить набор значимых характеристик объекта, исключая из рассмотрения
                    не значимые.Соответственно, абстракция — это набор всех таких характеристик.{' '}
                </strong>
            </p>
            <p>
                <div>Наследование. Тут более-менее все должно быть ясно, хотя бы на интуитивном уровне.</div>
                <strong>
                    Наследование — это свойство системы, позволяющее описать новый класс на основе уже существующего с
                    частично или полностью заимствующейся функциональностью. Класс, от которого производится
                    наследование, называется базовым, родительским или суперклассом. Новый класс — потомком, наследником
                    или производным классом
                </strong>
            </p>
            <div>Ну и классический пример с машинками. </div>
            <div>У нас есть класс - автомобиль.</div>
            <div>И дочерний класс - хэтчбеки.</div>
            Хэтчбэки наследуют у автомобиля 4 колеса, выхлопную трубу, способность ездить и прочие базовые
            характеристики и умения автомобиля. При этом у него будут свои характеристики, свойственные именно хэтчбеку.
        </p>
        <p>
            С инкапсуляцией есть некоторые тонкости. Часто выделяют СОКРЫТИЕ ДАННЫХ как основной смысл инкапсуляции,
            однако же сокрытие есть важный побочный продукт инкапсуляции. Суть в том, что
            <div>
                <strong>
                    Инкапсуляция — это свойство системы, позволяющее объединить данные и методы, работающие с ними в
                    классе
                </strong>
            </div>
            <div>
                или
                <div>
                    <strong>
                        В информатике размещение в одном компоненте данных и методов, которые с ними работают. Также
                        может означать скрытие внутренней реализации от других компонентов. Например, доступ к скрытой
                        переменной может предоставляться не напрямую, а с помощью методов для чтения и изменения её
                        значения
                    </strong>
                </div>
            </div>
            <div>
                Инкапсуляция повзоляет выстраивать архитектуру. Разбивать на отдельные модули. Предоставляет возможность
                удобства в пользовании компонентами. Обеспечивает безопасность сокрытием.
            </div>
        </p>
        <p>
            И наконец, его величество полиорфизм. Страшный дядька, которого в некоторых источниках выделяют как столп
            ООП, называя принципы выше лишь его подспорьем.
            <div>
                Может, так оно и есть. Ведь именно полиморфизм задает ноту универсальности интерфейсов. Позволяет
                выстраивать гибкую и расширяемую архитектуру.
            </div>
            <div>
                Ведь основной смысл ООП в том, что благодаря ему мы можем превратить скромный туду-лист в сложный
                многофункциональный органайзер по типу jira. При этом не сойдя с ума от того, что на каком-то этапе
                новый функционал добавлять стало невроятно сложно, баги сыпятся как из рога страданий,отловить их не
                может даже Чак Норрис, а запутаться в коде могут не только лишь все.{' '}
            </div>
            <div>
                Определений, как и типов полиморфизма много. Пока не буду на этом заострять внимание. Приведу наиболее
                лакончиные
            </div>
            <div>
                <strong>
                    Полиморфизм — это свойство системы использовать объекты с одинаковым интерфейсом без информации о
                    типе и внутренней структуре объекта.
                </strong>
            </div>
            <div>
                <strong>Полиморфизм — способность функции обрабатывать данные разных типов.</strong>
            </div>
            <div>
                <strong>
                    Полиморфизм — возможность объектов с одинаковой спецификацией иметь различную реализацию.
                </strong>
            </div>
        </p>

        <p></p>
        <p>Подведем итог.</p>
        <p>
            Смысл ООП - выстраивании гибкой, расширяемой и надежной архитектуры. Но основе ООП строится{' '}
            <Link to="/solid">solid</Link>. Которые повсеместно используются в создании приложений.
        </p>
    </div>
);

export default OOP;
