import React from 'react';

const DataStructures = () => (
    <div>
        <h3>Структуры данных</h3>
        <p>
            Мякотка(даже скорее опорный конспект)
            <a href="https://habr.com/ru/post/310794/">этого материала</a>
        </p>
        <p>Что нам дадут эти знания?</p>
        <div>
            Штмл верстальщику/jquery developer-у из 2013, быть может и ничего. Но мускулистому разработчику позволит:
        </div>
        <ul className="row list_style">
            <li>Управлять сложностью своих программ, делая их доступней для понимания.</li>
            <li>Создавать высокопроизводительные программы, эффективно работающие с памятью.</li>
        </ul>
        <h3>Что такое структуры данных?</h3>
        <p>
            По сути, это способы хранить и организовывать данные, чтобы эффективней решать различные задачи. Данные
            можно представить по-разному. В зависимости от того, что это за данные и что вы собираетесь с ними делать,
            одно представление подойдёт лучше других.
        </p>

        <h3>Алгоритмы</h3>

        <div>Алгоритм — такое хитроумное название для последовательности совершаемых действий.</div>
        <div>
            Структуры данных реализованы с помощью алгоритмов, алгоритмы — с помощью структур данных. Всё состоит из
            структур данных и алгоритмов, вплоть до уровня, на котором бегают микроскопические человечки с перфокартами
            и заставляют компьютер работать. Любая данная задача реализуется бесконечным количеством способов. Как
            следствие, для решения распространённых задач изобрели множество различных алгоритмов. Например, для
            сортировки неупорядоченного множества элементов существует до смешного большое количество алгоритмов:{' '}
        </div>

        <div>
            <ul className="list_style">
                <li>
                    <a href="https://habr.com/ru/post/335920/">
                        Сортировка вставками, Сортировка выбором, Сортировка слиянием, Сортировка пузырьком, Cортировка
                        кучи, Быстрая сортировка, Сортировка Шелла, Сортировка Тима, Блочная сортировка, Поразрядная
                        сортировка...
                    </a>
                </li>
            </ul>
        </div>

        <div>
            Некоторые из них значительно быстрее остальных. Другие занимают меньше памяти. Третьи легко реализовать.
            Четвёртые построены на допущениях относительно наборов данных.
        </div>

        <div>
            Каждая из сортировок подходит лучше других для определённой задачи. Поэтому вам надо будет сперва решить,
            какие у вас потребности и критерии, чтобы понять, как сравнивать алгоритмы между собой.
        </div>

        <div>
            Для сравнения производительности алгоритмов используется грубое измерение средней производительности и
            производительности в худшем случае, для обозначения которых используется термин
            <a href="https://webdevblog.ru/bolshoe-o-chto-eto-takoe-pochemu-eto-vazhno-i-pochemu-eto-ne-vazhno/">
                {' '}
                «О» большое.
            </a>
        </div>

        <div>
            «О» большое — обозначение способа приблизительной оценки производительности алгоритмов для относительного
            сравнения.
        </div>

        <div>
            О большое — заимствованное информатикой математические обозначение, определяющее, как алгоритмы соотносятся
            с передаваемым им некоторым количеством N данных.
        </div>

        <div>О большое характеризует две основные величины:</div>

        <div>
            Оценка времени выполнения — общее количество операций, которое алгоритм проведёт на данном множестве данных.
        </div>

        <div>
            Оценка объёма — общее количество памяти, требующееся алгоритму для обработки данного множества данных.
        </div>

        <div>
            Оценки делаются независимо друг от друга: одни алгоритмы могут производить меньше операций, чем другие,
            занимая при этом больше памяти. Определив свои требования, вы сможете выбрать соответствующий алгоритм.
        </div>

        <h3>Вот некоторые распространённые значения О большого:</h3>

        <p>взято с ориг истончика</p>
        <div>
            <img src={require('./assets/obig.png')} />
        </div>
        <h3>Структуры данных непосредственно</h3>
        <p>разделим на две группы: использующие память напрмую и реализующие другие структуры данных</p>
        <h3>Напрямую</h3>
        <p>Списки</p>
        <p>Хэш - таблицы</p>
        <h3>Через другие</h3>
        <p>Стэки</p>
        <p>Графы</p>
        <p>Связанные списки</p>
        <p>Деревья</p>
        <p></p>
    </div>
);

export default DataStructures;
