import React from 'react'
import {Link} from 'react-router-dom';

const Yagni = () =><div>
    <h3>Принцип программирования YAGNI — «Вам это не понадобится»</h3>
    <p> <a href='https://web-creator.ru/articles/yagni'>статья</a></p>
    <p>
    Если упрощенно, то следование данному принципу заключается в том, что возможности, которые не описаны в требованиях к системе, 
    просто не должны реализовываться. Это позволяет вести разработку, руководствуясь экономическими критериями — 
    Заказчик не должен оплачивать ненужные ему функции, а разработчики не должны тратить своё оплачиваемое время на реализацию того, 
    что не требуется.
    </p>
    <p>
    Основная проблема, которую решает принцип YAGNI — это устранение тяги программистов к излишней абстракции,
     к экспериментам «из интереса» и к реализации функционала, который сейчас не нужен, но, по мнению разработчика, может либо вскоре 
     понадобиться, либо просто будет полезен, хотя в реальности такого очень часто не происходит.
    </p>
    <p>
    «Бесплатных» функций в программных продуктах просто не бывает. Если рассматривать материальную сторону, то любые ненужные, 
    но фактически реализованные «фичи» оплачиваются либо Заказчиком (в бюджет закладываются расходы на те функции, которые не нужны), либо 
    Исполнителем из прибыли по проекту.
     И тот, и другой варианты с точки зрения бизнеса неверны. Если же говорить о нематериальных затратах, то любые «бонусные» 
     возможности усложняют сопровождение, увеличивают вероятность ошибок и усложняют взаимодействие с продуктом, — между объёмом кодовой базы и
      описанными характеристиками есть прямая зависимость. Больше написанного кода — труднее сопровождать и выше вероятность появления «багов», 
      тут очень уместна поговорка: «лучший код — это ненаписанный код».
    </p>
    <p>
    Принципы YAGNI и <Link to='/kiss'>KISS</Link> очень похожи, если <Link to='/kiss'>KISS</Link> нацелен на упрощение и полезен в плане работы с теми требованиями, которые имеют место быть, 
    то YAGNI более категоричен и применяется для ограждения проектов 
    по разработке ПО от «размывания» их рамок.
    </p>
    <p>
    <strong>
    Подход к реализации проектов строго по ТЗ верен с нескольких ракурсов. Заказчик не должен платить за то, что ему не надо, а продукт должен быть максимально сопровождаем и его качество не должно страдать от интеграции ненужных функций.
    </strong>
    </p>
</div>

export default Yagni